.user-mgmt-root {
  padding: 2.5rem 2rem;
  width: 100%;
  max-width: none;
  margin: 0;
  box-sizing: border-box;
}
.user-mgmt-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 1.5rem;
}
.user-mgmt-header h2 {
  font-size: 1.5rem;
  color: #2563eb;
  font-weight: 700;
  margin: 0;
}
.user-mgmt-add-btn {
  background: linear-gradient(90deg, #6366f1 0%, #2563eb 100%);
  color: #fff;
  border: none;
  border-radius: 0.5rem;
  font-size: 1rem;
  font-weight: 600;
  padding: 0.6rem 1.2rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  cursor: pointer;
  transition: background 0.18s, box-shadow 0.18s;
  box-shadow: 0 2px 8px rgba(99, 102, 241, 0.08);
}
.user-mgmt-add-btn:hover {
  background: linear-gradient(90deg, #2563eb 0%, #6366f1 100%);
}
.user-mgmt-error {
  color: #ef4444;
  background: #fef2f2;
  border: 1px solid #fecaca;
  border-radius: 0.5rem;
  padding: 0.5rem 1rem;
  margin-bottom: 1rem;
  font-size: 1rem;
}
.user-mgmt-table-wrap {
  overflow-x: auto;
  background: #fff;
  border-radius: 1rem;
  box-shadow: 0 2px 12px rgba(99, 102, 241, 0.07);
  padding: 1.5rem 0.5rem;
}
.user-mgmt-table {
  width: 100%;
  border-collapse: collapse;
  font-size: 1rem;
}
.user-mgmt-table th, .user-mgmt-table td {
  padding: 0.7rem 0.8rem;
  text-align: left;
}
.user-mgmt-table th {
  background: #e0e7ff;
  color: #2563eb;
  font-weight: 700;
  border-bottom: 2px solid #cbd5e1;
}
.user-mgmt-table tr:nth-child(even) {
  background: #f8fafc;
}
.user-mgmt-table tr:hover {
  background: #e0e7ff;
}
.user-mgmt-action-btn {
  background: #f1f5f9;
  border: none;
  border-radius: 0.4rem;
  color: #2563eb;
  font-size: 1.1rem;
  margin-right: 0.3rem;
  padding: 0.4rem 0.6rem;
  cursor: pointer;
  transition: background 0.18s, color 0.18s;
}
.user-mgmt-action-btn:hover {
  background: #6366f1;
  color: #fff;
}
.user-mgmt-action-btn.delete {
  background: #fef2f2;
  color: #ef4444;
}
.user-mgmt-action-btn.delete:hover {
  background: #ef4444;
  color: #fff;
}
.user-mgmt-modal {
  position: fixed;
  top: 0; left: 0; right: 0; bottom: 0;
  background: rgba(36, 41, 46, 0.18);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}
.user-mgmt-modal-content {
  background: #fff;
  border-radius: 1rem;
  box-shadow: 0 4px 32px rgba(60, 72, 100, 0.18);
  padding: 2rem 2.5rem 1.5rem 2.5rem;
  min-width: 340px;
  max-width: 95vw;
  display: flex;
  flex-direction: column;
  align-items: stretch;
}
.user-mgmt-modal-content h3 {
  margin-top: 0;
  color: #2563eb;
  font-size: 1.2rem;
  font-weight: 700;
  margin-bottom: 1.2rem;
}
.user-mgmt-form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.user-mgmt-form label {
  font-size: 1rem;
  color: #334155;
  font-weight: 500;
  display: flex;
  flex-direction: column;
  gap: 0.3rem;
}
.user-mgmt-form input, .user-mgmt-form select {
  padding: 0.6rem 1rem;
  border: 1px solid #cbd5e1;
  border-radius: 0.5rem;
  font-size: 1rem;
  outline: none;
  background: #f8fafc;
  transition: border 0.18s;
}
.user-mgmt-form input:focus, .user-mgmt-form select:focus {
  border: 1.5px solid #2563eb;
}
.user-mgmt-form-actions {
  display: flex;
  gap: 1rem;
  margin-top: 1.2rem;
  justify-content: flex-end;
}
.user-mgmt-save-btn {
  background: linear-gradient(90deg, #6366f1 0%, #2563eb 100%);
  color: #fff;
  border: none;
  border-radius: 0.5rem;
  font-size: 1rem;
  font-weight: 600;
  padding: 0.6rem 1.2rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  cursor: pointer;
  transition: background 0.18s, box-shadow 0.18s;
  box-shadow: 0 2px 8px rgba(99, 102, 241, 0.08);
}
.user-mgmt-save-btn:hover {
  background: linear-gradient(90deg, #2563eb 0%, #6366f1 100%);
}
.user-mgmt-cancel-btn {
  background: #f1f5f9;
  color: #2563eb;
  border: none;
  border-radius: 0.5rem;
  font-size: 1rem;
  font-weight: 600;
  padding: 0.6rem 1.2rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  cursor: pointer;
  transition: background 0.18s, color 0.18s;
}
.user-mgmt-cancel-btn:hover {
  background: #e0e7ff;
  color: #1e40af;
}
@media (max-width: 700px) {
  .user-mgmt-modal-content {
    padding: 1rem 0.5rem 0.5rem 0.5rem;
    min-width: 90vw;
  }
  .user-mgmt-root {
    padding: 1rem 0.2rem;
  }
  .user-mgmt-table th, .user-mgmt-table td {
    padding: 0.5rem 0.3rem;
    font-size: 0.98rem;
  }
} 