body {
  margin: 0;
  font-family: 'Segoe UI', 'Roboto', 'Arial', sans-serif;
  background: linear-gradient(135deg, #e0e7ff 0%, #f8fafc 100%);
  min-height: 100vh;
}

.login-container {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #e0e7ff 0%, #f8fafc 100%);
}

.login-box.modern-medical {
  background: #fff;
  padding: 2.5rem 2rem 2rem 2rem;
  border-radius: 1.5rem;
  box-shadow: 0 8px 32px rgba(60, 72, 100, 0.18);
  min-width: 350px;
  max-width: 95vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-top: 6px solid #2563eb;
  border-bottom: 2px solid #6366f1;
  position: relative;
}

.icon-header {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 0.5rem;
  background: #e0e7ff;
  border-radius: 50%;
  width: 70px;
  height: 70px;
  box-shadow: 0 2px 8px rgba(99, 102, 241, 0.10);
}

.login-box h1 {
  margin: 0 0 0.25rem 0;
  font-size: 2.3rem;
  color: #2563eb;
  font-weight: 800;
  letter-spacing: 1px;
}

.login-box h2 {
  margin: 0 0 2rem 0;
  font-size: 1.1rem;
  color: #64748b;
  font-weight: 400;
  letter-spacing: 0.5px;
}

/* Registration form enhancements */
.switch-link {
  margin-top: 1.2rem;
  text-align: center;
  color: #64748b;
  font-size: 1rem;
}
.switch-link span {
  color: #2563eb;
  font-weight: 600;
  cursor: pointer;
  margin-left: 0.3rem;
  transition: text-decoration 0.2s, color 0.2s;
}
.switch-link span:hover {
  text-decoration: underline;
  color: #1e40af;
}

.login-box form {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
  background: #f8fafc;
  border-radius: 1rem;
  padding: 2rem 1.5rem 1.5rem 1.5rem;
  box-shadow: 0 2px 12px rgba(99, 102, 241, 0.07);
  margin-top: 1rem;
}

.input-group {
  display: flex;
  align-items: center;
  background: #f1f5f9;
  border-radius: 0.5rem;
  border: 1px solid #cbd5e1;
  padding: 0.5rem 1rem;
  transition: border 0.2s, box-shadow 0.2s;
  box-shadow: 0 1px 2px rgba(99, 102, 241, 0.03);
}
.input-group:focus-within {
  border: 1.5px solid #2563eb;
  box-shadow: 0 0 0 2px #c7d2fe;
}
.input-icon {
  margin-right: 0.7rem;
  color: #2563eb;
  font-size: 1.2rem;
}
.login-box input[type="email"],
.login-box input[type="password"],
.login-box input[type="text"] {
  border: none;
  background: transparent;
  outline: none;
  font-size: 1rem;
  flex: 1;
  padding: 0.7rem 0;
}
.remember-me-row {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  margin-bottom: 0.2rem;
  font-size: 0.98rem;
  color: #475569;
}
.login-btn, .logout-btn {
  padding: 0.75rem 1rem;
  background: linear-gradient(90deg, #6366f1 0%, #2563eb 100%);
  color: #fff;
  border: none;
  border-radius: 0.5rem;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: background 0.2s, box-shadow 0.2s, transform 0.1s;
  box-shadow: 0 2px 8px rgba(99, 102, 241, 0.08);
  margin-top: 0.5rem;
}
.login-btn:hover:not(:disabled), .logout-btn:hover:not(:disabled) {
  background: linear-gradient(90deg, #2563eb 0%, #6366f1 100%);
  transform: translateY(-2px) scale(1.03);
  box-shadow: 0 4px 16px rgba(99, 102, 241, 0.13);
}
.login-btn:disabled {
  background: #a5b4fc;
  cursor: not-allowed;
}

.logout-btn {
  background: #f87171;
  margin-top: 1.5rem;
}

.error-msg {
  color: #ef4444;
  background: #fef2f2;
  border: 1px solid #fecaca;
  border-radius: 0.5rem;
  padding: 0.5rem 1rem;
  margin-top: 0.5rem;
  font-size: 0.95rem;
  text-align: left;
}

/* Responsive for mobile */
@media (max-width: 500px) {
  .login-box.modern-medical {
    min-width: 95vw;
    padding: 1.2rem 0.5rem 1rem 0.5rem;
  }
  .login-box form {
    padding: 1rem 0.2rem 0.7rem 0.2rem;
  }
}

/* Enhanced mobile responsiveness */
@media (max-width: 400px) {
  .login-box.modern-medical {
    min-width: 100vw;
    padding: 0.5rem 0.1rem 0.5rem 0.1rem;
    border-radius: 0.7rem;
  }
  .login-box h1 {
    font-size: 1.5rem;
  }
  .login-box h2 {
    font-size: 0.9rem;
  }
  .login-box form {
    padding: 0.5rem 0.1rem 0.5rem 0.1rem;
    border-radius: 0.5rem;
  }
  .input-group {
    padding: 0.3rem 0.5rem;
    font-size: 0.95rem;
  }
  .login-btn, .logout-btn {
    font-size: 0.95rem;
    padding: 0.5rem 0.7rem;
  }
}
