/* Modern Professional Theme Variables */
:root {
  --primary: #2563eb; /* Blue */
  --primary-light: #3b82f6; /* Lighter Blue */
  --primary-bg: #fff; /* White */
  --card-bg: #fff;
  --card-shadow: 0 4px 24px rgba(37, 99, 235, 0.08);
  --border-radius: 1.2rem;
  --font-main: 'Inter', 'Roboto', Arial, sans-serif;
}

body, .patients-root {
  background: var(--primary-bg);
  font-family: var(--font-main);
}

.patients-root {
  padding: 3rem 2.5rem 2.5rem 2.5rem;
  min-height: 100vh;
  width: 100%;
  max-width: none;
  margin: 0;
  box-sizing: border-box;
  background: var(--primary-bg);
}

.patients-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 2.2rem;
  background: var(--card-bg);
  border-radius: var(--border-radius);
  box-shadow: var(--card-shadow);
  padding: 1.5rem 2rem;
}
.patients-header h2 {
  font-size: 2rem;
  color: var(--primary);
  font-weight: 800;
  margin: 0;
  letter-spacing: -1px;
}

.patients-header-actions {
  display: flex;
  align-items: center;
  gap: 1.5rem;
}

.patients-add-btn, .patients-save-btn {
  background: linear-gradient(90deg, var(--primary-light) 0%, var(--primary) 100%);
  color: #fff;
  border: none;
  border-radius: 2rem;
  font-size: 1.08rem;
  font-weight: 700;
  padding: 0.7rem 1.6rem;
  display: flex;
  align-items: center;
  gap: 0.7rem;
  cursor: pointer;
  transition: background 0.18s, box-shadow 0.18s, transform 0.12s;
  box-shadow: 0 2px 12px rgba(37, 99, 235, 0.10);
  outline: none;
}
.patients-add-btn:hover, .patients-save-btn:hover {
  background: linear-gradient(90deg, var(--primary) 0%, var(--primary-light) 100%);
  transform: translateY(-2px) scale(1.03);
}

.patients-export-btn {
  background: #f1f5f9;
  color: var(--primary);
  border: none;
  border-radius: 2rem;
  font-size: 1.08rem;
  font-weight: 600;
  padding: 0.7rem 1.4rem;
  display: flex;
  align-items: center;
  gap: 0.7rem;
  cursor: pointer;
  transition: background 0.18s, color 0.18s, transform 0.12s;
  margin-left: 0.5rem;
  outline: none;
}
.patients-export-btn:hover {
  background: var(--primary-light);
  color: #fff;
  transform: translateY(-2px) scale(1.03);
}

.patients-search-box {
  display: flex;
  align-items: center;
  background: #f1f5f9;
  border: 1.5px solid #cbd5e1;
  border-radius: 2rem;
  padding: 0.4rem 1.2rem 0.4rem 1rem;
  box-shadow: 0 1px 6px rgba(37, 99, 235, 0.06);
  transition: border 0.18s, box-shadow 0.18s;
}
.patients-search-box:focus-within {
  border: 1.5px solid var(--primary);
  box-shadow: 0 0 0 2px #c7d2fe;
}
.patients-search-icon {
  color: var(--primary);
  font-size: 1.2rem;
  margin-right: 0.7rem;
}
.patients-search-box input {
  border: none;
  background: transparent;
  outline: none;
  font-size: 1.08rem;
  padding: 0.5rem 0;
  width: 220px;
  color: #334155;
}

.patients-filter {
  background: #f1f5f9;
  border: 1.5px solid #cbd5e1;
  border-radius: 2rem;
  padding: 0.4rem 1.2rem 0.4rem 1rem;
  font-size: 1.08rem;
  color: #334155;
  margin-left: 0.5rem;
  outline: none;
  transition: border 0.18s;
}
.patients-filter:focus {
  border: 1.5px solid var(--primary);
}

.patients-table-wrap {
  overflow-x: auto;
  background: var(--card-bg);
  border-radius: var(--border-radius);
  box-shadow: var(--card-shadow);
  padding: 2.2rem 1.2rem 1.2rem 1.2rem;
  margin-bottom: 2.5rem;
}

.patients-table {
  width: 100%;
  border-collapse: separate;
  border-spacing: 0;
  font-size: 1.08rem;
  background: transparent;
  border-radius: var(--border-radius);
  overflow: hidden;
  box-shadow: 0 2px 12px rgba(37, 99, 235, 0.07);
}
.patients-table th, .patients-table td {
  padding: 1rem 1.1rem;
  text-align: left;
}
.patients-table th {
  background: #e0e7ff;
  color: var(--primary);
  font-weight: 800;
  border-bottom: 2px solid #cbd5e1;
  position: sticky;
  top: 0;
  z-index: 2;
  font-size: 1.1rem;
}
.patients-table tr:nth-child(even) {
  background: #f8fafc;
}
.patients-table tr {
  transition: background 0.18s;
}
.patients-table tr:hover {
  background: #e0e7ff;
}
.patients-table td {
  border-bottom: 1px solid #f1f5f9;
}
.patients-table th:first-child, .patients-table td:first-child {
  border-top-left-radius: 1rem;
}
.patients-table th:last-child, .patients-table td:last-child {
  border-top-right-radius: 1rem;
}

.patients-action-btn {
  background: #f1f5f9;
  border: none;
  border-radius: 50%;
  color: var(--primary);
  font-size: 1.2rem;
  margin-right: 0.3rem;
  padding: 0.5rem 0.6rem;
  cursor: pointer;
  transition: background 0.18s, color 0.18s, transform 0.12s;
  outline: none;
  box-shadow: 0 1px 4px rgba(37, 99, 235, 0.06);
}
.patients-action-btn:hover {
  background: var(--primary-light);
  color: #fff;
  transform: scale(1.08);
}
.patients-action-btn.delete {
  background: #fef2f2;
  color: #ef4444;
}
.patients-action-btn.delete:hover {
  background: #ef4444;
  color: #fff;
}
.patients-action-btn.lab {
  background: #e0e7ff;
  color: var(--primary);
}
.patients-action-btn.lab:hover {
  background: var(--primary);
  color: #fff;
}

.patients-error {
  color: #ef4444;
  background: #fef2f2;
  border: 1px solid #fecaca;
  border-radius: 0.7rem;
  padding: 0.7rem 1.2rem;
  margin-bottom: 1.2rem;
  font-size: 1.08rem;
  font-weight: 500;
}

.patients-modal {
  position: fixed; /* changed from absolute to fixed for better positioning */
  top: 0; left: 0; right: 0; bottom: 0;
  background: rgba(36, 41, 46, 0.22);
  backdrop-filter: blur(2px);
  display: flex;
  align-items: center; /* changed from flex-start to center */
  justify-content: center;
  z-index: 1000;
  padding: 2.5vh;
  overflow-y: auto; /* allow scrolling if content is too tall */
  height: 100vh; /* ensure full height */
  min-height: 100vh; /* ensure full height */
}
.patients-modal-content,
.patients-modal-content.urinalysis-modal {
  max-height: none !important;
  min-height: unset !important;
  height: auto !important;
  overflow: visible !important;
  display: block !important;
  margin-bottom: 2rem;
}
@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}
.patients-modal-content {
  background: var(--card-bg);
  border-radius: var(--border-radius);
  box-shadow: 0 8px 40px rgba(60, 72, 100, 0.18);
  padding: 2.5rem 3rem 2rem 3rem;
  min-width: 340px;
  max-width: 95vw;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  animation: modalPop 0.22s;
}
@keyframes modalPop {
  from { transform: scale(0.97) translateY(20px); opacity: 0; }
  to { transform: scale(1) translateY(0); opacity: 1; }
}
.patients-modal-content h3 {
  margin-top: 0;
  color: var(--primary);
  font-size: 1.3rem;
  font-weight: 800;
  margin-bottom: 1.5rem;
  letter-spacing: -0.5px;
}
.patients-modal-content h3 svg {
  vertical-align: middle;
}

.patients-form {
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
}
.patients-form label {
  font-size: 1.08rem;
  color: #334155;
  font-weight: 600;
  display: flex;
  flex-direction: column;
  gap: 0.3rem;
}
.patients-form input, .patients-form select {
  padding: 0.7rem 1.2rem;
  border: 1.5px solid #cbd5e1;
  border-radius: 0.7rem;
  font-size: 1.08rem;
  outline: none;
  background: #f8fafc;
  transition: border 0.18s, box-shadow 0.18s;
}
.patients-form input:focus, .patients-form select:focus {
  border: 1.5px solid var(--primary);
  box-shadow: 0 0 0 2px #c7d2fe;
}
.patients-form-actions {
  display: flex;
  gap: 1.2rem;
  margin-top: 1.5rem;
  justify-content: flex-end;
}
.patients-cancel-btn {
  background: #f1f5f9;
  color: var(--primary);
  border: none;
  border-radius: 2rem;
  font-size: 1.08rem;
  font-weight: 700;
  padding: 0.7rem 1.6rem;
  display: flex;
  align-items: center;
  gap: 0.7rem;
  cursor: pointer;
  transition: background 0.18s, color 0.18s, transform 0.12s;
}
.patients-cancel-btn:hover {
  background: #e0e7ff;
  color: #1e40af;
  transform: scale(1.03);
}

.patients-detail-list {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 0.3rem 1.5rem;
  font-size: 1.05rem;
  color: #334155;
  margin-bottom: 1.2rem;
  background: #f8fafc;
  border-radius: 0.7rem;
  padding: 0.7rem 1.2rem 0.7rem 1.2rem;
  box-shadow: 0 1px 6px rgba(99, 102, 241, 0.04);
  border: 1px solid #e5e7eb;
}
.patients-detail-list > div {
  display: flex;
  align-items: center;
  padding: 0.2rem 0;
  border-bottom: 1px solid #e5e7eb;
}
.patients-detail-list > div:nth-last-child(-n+2) {
  border-bottom: none;
}
.patients-detail-list b {
  min-width: 110px;
  font-weight: 600;
  color: #2563eb;
  margin-right: 0.5rem;
}
@media (max-width: 700px) {
  .patients-detail-list {
    grid-template-columns: 1fr;
    gap: 0.2rem 0;
    padding: 0.7rem 0.5rem;
  }
}

/* Lab Test Page Enhancements */
.patients-lab-info {
  margin-bottom: 2.2rem;
}
.patients-lab-history {
  margin-bottom: 2.2rem;
}
.patients-lab-add {
  background: #f8fafc;
  border-radius: 0.7rem;
  padding: 1.5rem 2rem;
  box-shadow: 0 1px 6px rgba(99, 102, 241, 0.04);
}

.patients-back-btn {
  background: #f1f5f9;
  color: var(--primary);
  border: none;
  border-radius: 2rem;
  font-size: 1.08rem;
  font-weight: 600;
  padding: 0.6rem 1.3rem;
  display: flex;
  align-items: center;
  gap: 0.6rem;
  cursor: pointer;
  transition: background 0.18s, color 0.18s, transform 0.12s;
  outline: none;
}
.patients-back-btn:hover {
  background: var(--primary-light);
  color: #fff;
  transform: scale(1.03);
}

.patients-lab-tab {
  background: #f1f5f9;
  color: var(--primary);
  border: none;
  border-radius: 2rem 2rem 0 0;
  font-size: 1.08rem;
  font-weight: 700;
  padding: 0.7rem 2.2rem;
  cursor: pointer;
  transition: background 0.18s, color 0.18s, box-shadow 0.18s;
  margin-bottom: -2px;
  border-bottom: 2px solid transparent;
  outline: none;
}
.patients-lab-tab.active {
  background: #fff;
  color: var(--primary);
  border-bottom: 2px solid var(--primary);
  box-shadow: 0 2px 12px rgba(99, 102, 241, 0.10);
  z-index: 1;
}
.patients-lab-tab:disabled {
  color: #b6b6b6;
  background: #f4f6fb;
  cursor: not-allowed;
  border-bottom: 2px solid transparent;
}

/* Responsive Design */
@media (max-width: 900px) {
  .patients-root {
    padding: 1.2rem 0.2rem;
  }
  .patients-modal-content {
    padding: 1.2rem 0.5rem 0.5rem 0.5rem;
    min-width: 90vw;
  }
  .patients-table th, .patients-table td {
    padding: 0.7rem 0.3rem;
    font-size: 1rem;
  }
  .patients-search-box input {
    width: 120px;
  }
}

/* Enhanced mobile responsiveness */
@media (max-width: 600px) {
  .patients-header {
    flex-direction: column;
    align-items: flex-start;
    padding: 1rem 0.5rem;
    gap: 1rem;
  }
  .patients-header-actions {
    flex-wrap: wrap;
    gap: 0.7rem;
    width: 100%;
  }
  .patients-table-wrap {
    padding: 1rem 0.2rem 0.7rem 0.2rem;
  }
  .patients-table th, .patients-table td {
    font-size: 0.95rem;
    padding: 0.5rem 0.2rem;
  }
  .patients-form label, .patients-form input, .patients-form select, .patients-form-actions, .patients-add-btn, .patients-save-btn, .patients-cancel-btn {
    width: 100%;
    box-sizing: border-box;
  }
  .patients-form-actions {
    flex-direction: column;
    gap: 0.7rem;
    margin-top: 1rem;
  }
  .patients-modal-content {
    padding: 0.7rem 0.2rem 0.5rem 0.2rem;
    min-width: 98vw;
  }
  .patients-lab-tab {
    padding: 0.5rem 0.7rem;
    font-size: 0.95rem;
  }
  .patients-detail-list {
    font-size: 0.95rem;
    padding: 0.5rem 0.2rem;
  }
  .patients-back-btn {
    width: 100%;
    justify-content: center;
  }
} 

/* Enhanced Urinalysis Modal */
.patients-modal-content.urinalysis-modal-enhanced {
  max-width: 800px;
  width: 90vw;
  max-height: 95vh;
  min-width: unset;
  padding: 0;
  box-sizing: border-box;
  background: #fff;
  border-radius: 1.2rem;
  box-shadow: 0 8px 32px rgba(37,99,235,0.13);
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.urinalysis-modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem 2rem;
  background: linear-gradient(135deg, #f8fafc 0%, #e0e7ff 100%);
  border-bottom: 1px solid #e2e8f0;
  border-radius: 1.2rem 1.2rem 0 0;
}

.urinalysis-modal-header h3 {
  margin: 0;
  color: var(--primary);
  font-size: 1.3rem;
  font-weight: 800;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.urinalysis-close-btn {
  background: #f1f5f9;
  border: none;
  border-radius: 50%;
  width: 2.5rem;
  height: 2.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  color: var(--primary);
  font-size: 1.1rem;
  transition: all 0.18s;
}

.urinalysis-close-btn:hover {
  background: #ef4444;
  color: #fff;
  transform: scale(1.05);
}

.urinalysis-tabs-container {
  display: flex;
  padding: 0 2rem;
  background: #f8fafc;
  border-bottom: 1px solid #e2e8f0;
  gap: 0.5rem;
}

.urinalysis-tab-btn {
  background: transparent;
  border: none;
  border-radius: 0.5rem 0.5rem 0 0;
  padding: 0.8rem 1.5rem;
  font-size: 1.08rem;
  font-weight: 700;
  color: #64748b;
  cursor: pointer;
  transition: all 0.18s;
  position: relative;
  margin-bottom: -1px;
}

.urinalysis-tab-btn.active {
  background: #fff;
  color: var(--primary);
  border-bottom: 2px solid var(--primary);
}

.urinalysis-tab-btn:hover:not(.active) {
  background: #e0e7ff;
  color: var(--primary);
}

.urinalysis-content {
  flex: 1;
  overflow-y: auto;
  padding: 1.5rem 2rem;
  background: #fff;
}

.urinalysis-form-enhanced {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.urinalysis-form-section {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.urinalysis-section-title {
  color: var(--primary);
  font-size: 1.2rem;
  font-weight: 700;
  margin: 0;
  padding-bottom: 0.5rem;
  border-bottom: 2px solid #e0e7ff;
}

.urinalysis-form-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  gap: 1rem;
}

.urinalysis-form-field {
  display: flex;
  flex-direction: column;
  gap: 0.3rem;
}

.urinalysis-form-field.urinalysis-full-width {
  grid-column: 1 / -1;
}

.urinalysis-form-field label {
  font-size: 1.08rem;
  font-weight: 600;
  color: #2563eb;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  margin-bottom: 0.2rem;
}

.normal-range {
  font-size: 0.9rem;
  color: #64748b;
  font-weight: 500;
  background: #f1f5f9;
  padding: 0.2rem 0.5rem;
  border-radius: 0.4rem;
}

.urinalysis-form-field input {
  padding: 0.7rem 1rem;
  border: 1.5px solid #cbd5e1;
  border-radius: 0.7rem;
  font-size: 1.08rem;
  outline: none;
  background: #fff;
  transition: border 0.18s, box-shadow 0.18s;
}

.urinalysis-form-field input:focus {
  border: 1.5px solid var(--primary);
  box-shadow: 0 0 0 2px rgba(37, 99, 235, 0.1);
}

.urinalysis-form-field input::placeholder {
  color: #94a3b8;
  font-style: italic;
}

.urinalysis-form-actions {
  display: flex;
  gap: 1rem;
  justify-content: flex-end;
  padding-top: 1rem;
  border-top: 1px solid #e2e8f0;
  margin-top: 1rem;
}

/* Responsive Design */
@media (max-width: 1024px) {
  .patients-modal-content.urinalysis-modal-enhanced {
    width: 95vw;
    max-width: 95vw;
  }
}

@media (max-width: 768px) {
  .patients-modal-content.urinalysis-modal-enhanced {
    max-width: 100vw;
    max-height: 100vh;
    border-radius: 0;
    width: 100vw;
  }
  
  .urinalysis-modal-header {
    padding: 1rem 1.5rem;
    border-radius: 0;
  }
  
  .urinalysis-modal-header h3 {
    font-size: 1.2rem;
  }
  
  .urinalysis-tabs-container {
    padding: 0 1.5rem;
  }
  
  .urinalysis-content {
    padding: 1rem 1.5rem;
  }
  
  .urinalysis-form-grid {
    grid-template-columns: 1fr;
    gap: 0.8rem;
  }
}

@media (max-width: 600px) {
  .urinalysis-modal-header {
    padding: 0.8rem 1rem;
    flex-direction: column;
    gap: 0.5rem;
    align-items: flex-start;
  }
  
  .urinalysis-close-btn {
    position: absolute;
    top: 0.8rem;
    right: 1rem;
    width: 2rem;
    height: 2rem;
  }
  
  .urinalysis-tabs-container {
    padding: 0 1rem;
    gap: 0.3rem;
  }
  
  .urinalysis-tab-btn {
    padding: 0.6rem 1rem;
    font-size: 1rem;
  }
  
  .urinalysis-content {
    padding: 1rem;
  }
  
  .urinalysis-form-actions {
    flex-direction: column;
    gap: 0.8rem;
  }
}

