    flex-direction: column;
    gap: 1rem;
    align-items: flex-start;
  }
  
  .patient-status-indicators {
    flex-direction: column;
    gap: 0.5rem;
  }
  
  .enhanced-main-table {
    font-size: 0.9rem;
  }
  
  .enhanced-main-table th,
  .enhanced-main-table td {
    padding: 0.8rem 0.5rem;
  }
  
  .action-buttons {
    flex-direction: column;
    gap: 0.3rem;
  }
} 

/* Modern Urinalysis Tabs */
.modern-tabs {
  display: flex;
  gap: 0.5rem;
  margin-bottom: 1.5rem;
  border-bottom: 2px solid #e5e7eb;
  background: transparent;
}
.modern-tab-btn {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem 1.5rem;
  background: #f9fafb;
  border: none;
  border-radius: 1.5rem 1.5rem 0 0;
  font-size: 1rem;
  font-weight: 500;
  color: #64748b;
  cursor: pointer;
  transition: background 0.2s, color 0.2s, box-shadow 0.2s;
  box-shadow: none;
  position: relative;
  outline: none;
}
.modern-tab-btn .tab-icon {
  font-size: 1.2em;
  margin-right: 0.25em;
}
.modern-tab-btn.active, .modern-tab-btn:focus {
  background: #fff;
  color: #2563eb;
  box-shadow: 0 2px 8px rgba(37,99,235,0.08);
  border-bottom: 2px solid #2563eb;
  z-index: 2;
}
.modern-tab-btn:not(.active):hover {
  background: #f1f5f9;
  color: #1e293b;
}
@media (max-width: 600px) {
  .modern-tabs {
    gap: 0.25rem;
  }
  .modern-tab-btn {
    padding: 0.5rem 0.75rem;
    font-size: 0.95rem;
  }
} 