.dashboard-root {
  display: flex;
  min-height: 100vh;
  background: linear-gradient(135deg, #e0e7ff 0%, #f8fafc 100%);
}

.dashboard-sidebar {
  width: 240px;
  background: #fff;
  box-shadow: 2px 0 12px rgba(60, 72, 100, 0.08);
  display: flex;
  flex-direction: column;
  padding: 2rem 1rem 1rem 1rem;
}

.sidebar-header {
  display: flex;
  align-items: center;
  gap: 0.7rem;
  margin-bottom: 2.5rem;
}
.sidebar-title {
  font-size: 1.5rem;
  font-weight: 700;
  color: #2563eb;
  letter-spacing: 1px;
}
.sidebar-menu {
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
}
.sidebar-menu-item {
  display: flex;
  align-items: center;
  gap: 0.8rem;
  font-size: 1.08rem;
  color: #334155;
  padding: 0.7rem 1rem;
  border-radius: 0.5rem;
  cursor: pointer;
  transition: background 0.18s, color 0.18s;
  text-decoration: none;
  border: none;
}
.sidebar-menu-item:visited {
  text-decoration: none;
}
.sidebar-menu-item span {
  text-decoration: none;
}
.sidebar-menu-item:hover {
  background: #e0e7ff;
  color: #2563eb;
}
.sidebar-icon {
  font-size: 1.2rem;
  color: #2563eb;
}

.dashboard-main {
  flex: 1;
  padding: 2.5rem 3rem 2rem 3rem;
  display: flex;
  flex-direction: column;
  background: transparent;
}
.dashboard-header {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  background: #fff;
  border-radius: 1rem 1rem 0 0;
  box-shadow: 0 2px 8px rgba(99, 102, 241, 0.04);
  padding: 1rem 2rem;
  min-height: 64px;
  margin-bottom: 1.5rem;
}
.dashboard-header.modern-header {
  justify-content: flex-end;
  background: #fff;
  border-bottom: 1px solid #e0e7ff;
  box-shadow: 0 2px 8px rgba(99, 102, 241, 0.04);
  border-radius: 1rem 1rem 0 0;
  min-height: 64px;
  margin-bottom: 1.5rem;
}
.header-left {
  flex: 1;
}
.header-right {
  display: flex;
  align-items: center;
  gap: 1.2rem;
}
.header-user {
  display: flex;
  align-items: center;
  gap: 0.7rem;
  background: #f1f5f9;
  border-radius: 2rem;
  padding: 0.3rem 1rem 0.3rem 0.5rem;
  font-size: 1rem;
  color: #2563eb;
  font-weight: 600;
}
.header-user-avatar {
  background: #e0e7ff;
  border-radius: 50%;
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.header-user-name {
  color: #2563eb;
  font-weight: 600;
  font-size: 1rem;
}
.header-user-profession {
  color: #64748b;
  font-size: 0.98rem;
  font-weight: 500;
  margin-left: 0.7rem;
  background: #e0e7ff;
  border-radius: 1rem;
  padding: 0.2rem 0.8rem;
  display: inline-block;
}
.header-logout-btn {
  background: #f87171;
  color: #fff;
  border: none;
  border-radius: 50%;
  width: 38px;
  height: 38px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: background 0.18s, box-shadow 0.18s;
  box-shadow: 0 2px 8px rgba(248, 113, 113, 0.08);
}
.header-logout-btn:hover {
  background: #dc2626;
}

.dashboard-header h1,
.dashboard-header h2 {
  display: none;
}

.profile-box {
  display: flex;
  align-items: center;
  background: #fff;
  border-radius: 1rem;
  box-shadow: 0 2px 12px rgba(99, 102, 241, 0.07);
  padding: 1.5rem 2rem;
  margin-bottom: 2rem;
  gap: 1.5rem;
  max-width: 420px;
}
.profile-avatar {
  background: #e0e7ff;
  border-radius: 50%;
  width: 70px;
  height: 70px;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 2px 8px rgba(99, 102, 241, 0.10);
}
.profile-info {
  display: flex;
  flex-direction: column;
  gap: 0.2rem;
}
.profile-name {
  font-size: 1.2rem;
  font-weight: 700;
  color: #2563eb;
}
.profile-role {
  font-size: 1rem;
  color: #64748b;
}
.profile-email {
  font-size: 0.98rem;
  color: #334155;
}

.sidebar-menu-item.active {
  background: #2563eb;
  color: #fff;
}
.sidebar-menu-item.active .sidebar-icon {
  color: #fff;
}
.dashboard-content {
  background: #f8fafc;
  border-radius: 1rem;
  box-shadow: 0 2px 12px rgba(99, 102, 241, 0.04);
  padding: 2rem;
  min-height: 300px;
}

@media (max-width: 900px) {
  .dashboard-main {
    padding: 1.5rem 0.5rem 1rem 0.5rem;
  }
  .dashboard-sidebar {
    width: 70px;
    padding: 1rem 0.2rem 1rem 0.2rem;
  }
  .sidebar-title {
    display: none;
  }
  .sidebar-header {
    justify-content: center;
  }
}

@media (max-width: 600px) {
  .dashboard-root {
    flex-direction: column;
  }
  .dashboard-sidebar {
    width: 100vw;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    box-shadow: 0 2px 12px rgba(60, 72, 100, 0.08);
    padding: 0.5rem 0;
  }
  .dashboard-main {
    padding: 1rem 0.2rem 0.5rem 0.2rem;
  }
  .profile-box {
    flex-direction: column;
    align-items: flex-start;
    padding: 1rem;
    gap: 0.7rem;
    max-width: 100vw;
  }
  .dashboard-header {
    flex-direction: column;
    align-items: flex-start;
    padding: 0.7rem 0.2rem;
    min-height: unset;
  }
  .dashboard-content {
    padding: 1rem 0.2rem 0.5rem 0.2rem;
  }
} 